import React from 'react'
import nointeimg from '../image/No connection.svg'

export default function Nonatework() {
    return (
        <div>
            <div className="nonatework" id="status">
                <div className="popupnet circle">
                    <img src={nointeimg} alt="img" />
                    <p>No Internet Connection</p>
                </div>
            </div>
        </div>
    )
}
