import React, { useState } from "react";
import playbtnm from '../image/icons8-play-96.png'
import {
    Link
} from "react-router-dom";

export default function Croods() {
    const [isActive, setActive] = useState("false");

    const movievideo = () => {
        setActive(!isActive);
    };

    return (
        <div>
            <div className="main-movie-frem">
                <div className="image-move-themal">
                    <img src="https://i.ibb.co/t8fStGB/croods.jpg"
                        alt="croods" border="0" />
                </div>
                <div className="play-btn-movie">
                    <button className="center-play animatio-bump" id="url" onClick={movievideo}>
                        <div className="btn-play">
                            <img src={playbtnm} alt="" />
                        </div>
                    </button>
                </div>
                <div className="card-movie-content">
                    <div className="card-movie-content-head">
                        <Link to="#">
                            <h3 className="card-movie-title">Animation: Croods</h3>
                        </Link>
                        <div className="ratings">
                            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px"
                                fill="#000000">
                                <path d="M0 0h24v24H0z" fill="none" />
                                <path d="M16 6l2.29 2.29-4.88 4.88-4-4L2 16.59 3.41 18l6-6 4 4 6.3-6.29L22 12V6z" />
                            </svg>
                            <span>7.2</span>/10
                        </div>
                    </div>
                    <div className="card-movie-info">
                        <div className="movie-running-time">
                            <label>Upload Date</label>
                            <span>19 April 2013 - <span className="rade-color">English / Hindi</span></span>
                        </div>
                        <div className="movie-running-time">
                            <label>Video Length</label>
                            <span>1hr 38min</span>
                        </div>
                    </div>
                </div>
                <div className="download-button-area">
                    <p>Download Movie</p>
                    <div className="btns">
                        <a href="https://www.filmyzilla.run/download/6642/server_1">(Size
                            : 363.20 MB) 480p Download</a>
                        <a href="http://www.moviesyug.net/assets/files/729/The Croods (2013) (BRRip) (PC HD Full Movie).mp4">(Size
                            : 743.69 MB) 780p Download</a>
                    </div>
                </div>
            </div>
            <div class="backgrod-short">
                <div class="main-short">
                    <h2>Screen Shot</h2>
                    <img src="https://i.imgur.com/7QJNeaS.jpg" />
                </div>
            </div>
            <div className={`video-player-area-main movi-popup ${isActive ? "" : "trans100"}`} id="video-play">
                <div className="movie-areaman">
                    <p>Movie Player</p>
                    <div className="close-mov" onClick={movievideo}>
                        <a href="#" className='closebtn'>&times;</a>
                    </div>
                    <video id="video" controls src={`${isActive ? "" : "http://www.moviesyug.net/assets/files/729/The Croods (2013) (BRRip) (PC HD Full Movie).mp4"}`} autoPlay>
                    </video>
                </div>
            </div>
        </div>
    )
}
