import React from 'react'
import svgimg from '../image/404.svg'
import {
  Link
} from "react-router-dom";

const ErrorPage = () => {
    return (
        <div className="fullpageerreo">
            <img src={svgimg} alt="" />
            <p>Sorry, this page doesn't exist</p>
            <Link to="/">Go Back</Link>
        </div>
    )
}

export default ErrorPage
