import './maincss/Short.css';

import Homemanu from './Mycomponents/All_sidemanu/Homemanu';
import Scifinavebar from './Mycomponents/All_sidemanu/Scifinavebar';
import Animationnavbar from './Mycomponents/All_sidemanu/Animationnavbar'
import Newmovienavbar from './Mycomponents/All_sidemanu/Newmovienavbar'

import Footer from './Mycomponents/Footer';
import Imageslidf from './Mycomponents/Imageslidf';
import Moviecard from './Mycomponents/Moviecard';
import Morebon from './components/Morebon';
import Nonatework from './Mycomponents/Nonatework';
import Movetop from './components/Movetop';

import ScifimovieAll from './Mycomponents/ScifimovieAll'
import AnimationmovieAll from './Mycomponents/AnimationmovieAll'
