import Avengers_Endgame from './All_Movie/Avengers_Endgame'
import Croods from './All_Movie/Croods'
import AntManAndTheWasp2018 from './All_Movie/Ant Man And The Wasp 2018'
import Starwarstheriseofskywalker from "./All_Movie/Star wars the rise of skywalker";
import ManofSteel from './All_Movie/Man of Steel'
